.products {
  background-color: $color-black-pearl;
}

.products__container {
  padding: 70px 0 130px;
  display: flex;
  flex-direction: column;

  @include vp-1023 {
    align-items: center;
    padding: 60px 0 65px;
  }

  @include vp-767 {
    padding: 50px 0 76px;
  }
}

.products__title {
  margin: 0 0 50px 0;
  color: $color-default-white;

  @include vp-767 {
    margin-bottom: 40px;
    font-size: 28px;
    line-height: 40px;
  }
}

.products__list {
  list-style-type: none;
  margin: 0 0 10px 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 85px;

  @include vp-1023 {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}
